import React, { useState, useEffect } from 'react';
import { useAuth } from "../../common/context/AuthContext";
// import AdminNavbar removed - handled by ConditionalNavbar in App.js
import { SkeletonFullPage } from '../components/skeletons';
import {
  UnifiedBackground,
  UnifiedCard,
  UnifiedButton,
  useScrollAnimation,
  SharedStyles
} from "../../common/components/SharedTheme";
import commissionService, { COMMISSION_CONFIG } from '../../services/commissionService';
import { supabase } from "../../common/services/supabaseClient";
import { Clock, CheckCircle, XCircle, AlertCircle, Activity, Search, ChevronLeft, ChevronRight, Filter, ChevronDown, ChevronUp, Calendar } from 'lucide-react';

// Format transaction ID
const formatTransactionId = (commission) => {
  if (commission.transaction_id?.match(/^COM-\d{5}$/)) {
    return commission.transaction_id;
  }
  return `COM-${String(commission.id || '').slice(0, 5).padStart(5, '0')}`;
};

function AffiliateCommissions() {
  const { user } = useAuth();
  useScrollAnimation();

  // State management
  const [initialLoading, setInitialLoading] = useState(true);
  const [loadingMore, setLoadingMore] = useState(false);
  const [initialDataLoaded, setInitialDataLoaded] = useState(false);
  const [commissionHistory, setCommissionHistory] = useState([]);
  const [filteredHistory, setFilteredHistory] = useState([]);
  const [filters, setFilters] = useState({ 
    level: '', 
    status: '', 
    search: '',
    startDate: '',
    endDate: ''
  });
  const [promoterDetails, setPromoterDetails] = useState({});
  const [customerDetails, setCustomerDetails] = useState({});
  const [toast, setToast] = useState({ show: false, message: '', type: '' });
  const [hasMoreData, setHasMoreData] = useState(true);
  const [page, setPage] = useState(1);
  const [showFilters, setShowFilters] = useState(false);
  const PAGE_SIZE = 50;

  // Pagination state
  const [currentPage, setCurrentPage] = useState(1);
  const [entriesPerPage, setEntriesPerPage] = useState(10);

  // Toast utility
  const showToast = (message, type = 'success') => {
    setToast({ show: true, message, type });
    setTimeout(() => {
      setToast({ show: false, message: '', type: '' });
    }, 4000);
  };



  // Load initial set of commissions (fast)
  const loadInitialCommissionData = async () => {
    try {
      setInitialLoading(true);
      const historyResult = await commissionService.getCommissionHistory({
        limit: 10, // Load only first 10 records initially
        skip: 0
      });

      if (historyResult.success && historyResult.data?.length > 0) {
        // Remove duplicates from initial data
        const uniqueData = commissionService.removeDuplicateCommissions(historyResult.data);
        
        setCommissionHistory(uniqueData);
        setFilteredHistory(uniqueData);
        setInitialDataLoaded(true);

        // Start loading rest in background
        if (uniqueData.length >= 10) {
          loadMoreCommissions();
        } else {
          setHasMoreData(false);
        }
      } else {
        // Fallback to localStorage if needed
        const auditTrail = JSON.parse(localStorage.getItem('commission_audit_trail') || '[]');
        const initialBatch = auditTrail.slice(0, 10);
        setCommissionHistory(initialBatch);
        setFilteredHistory(initialBatch);
        setInitialDataLoaded(true);

        if (auditTrail.length > 10) {
          loadMoreCommissions();
        } else {
          setHasMoreData(false);
        }
      }
    } catch (error) {
      setCommissionHistory([]);
      setFilteredHistory([]);
      setInitialDataLoaded(true);
    } finally {
      setInitialLoading(false);
    }
  };

  // Load more commissions (pagination)
  const loadMoreCommissions = async () => {
    if (loadingMore || !hasMoreData) return;

    try {
      setLoadingMore(true);
      const historyResult = await commissionService.getCommissionHistory({
        limit: PAGE_SIZE,
        skip: (page - 1) * PAGE_SIZE
      });

      if (historyResult.success) {
        if (historyResult.data.length === 0) {
          setHasMoreData(false);
          return;
        }

        // Append new data without duplicates
        setCommissionHistory(prev => {
          const existingIds = new Set(prev.map(item => item.id));
          const newItems = commissionService.removeDuplicateCommissions(historyResult.data)
            .filter(item => !existingIds.has(item.id));
          
          return [...prev, ...newItems];
        });
        setPage(prev => prev + 1);

        // If we got less items than requested, we've reached the end
        if (historyResult.data.length < PAGE_SIZE) {
          setHasMoreData(false);
        }
      }
    } catch (error) {
      showToast('Error loading more commissions', 'error');
    } finally {
      setLoadingMore(false);
    }
  };

  // Handle scroll to load more
  const handleScroll = (e) => {
    const { scrollTop, scrollHeight, clientHeight } = e.target.documentElement;
    if (scrollTop + clientHeight >= scrollHeight - 20 && !loadingMore && hasMoreData) {
      loadMoreCommissions();
    }
  };

  // Set up scroll event listener
  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [loadingMore, hasMoreData]);

  // Handle filter change
  const handleFilterChange = (key, value) => {
    setFilters(prev => ({ ...prev, [key]: value }));
  };

  // Toggle filters panel
  const toggleFilters = () => setShowFilters(!showFilters);

  // Clear all filters
  const clearFilters = () => {
    setFilters({
      level: '',
      status: '',
      search: '',
      startDate: '',
      endDate: ''
    });  
  };

  // Apply filters to commission history
  useEffect(() => {
    const uniqueCommissionHistory = commissionService.removeDuplicateCommissions(commissionHistory);
    let filtered = [...uniqueCommissionHistory];

    // Apply level filter
    if (filters.level) {
      const levelValue = parseInt(filters.level);
      filtered = filtered.filter(commission => {
        const commissionLevel = typeof commission.level === 'string' 
          ? parseInt(commission.level) 
          : commission.level;
        return commissionLevel === levelValue;
      });
    }

    // Apply status filter
    if (filters.status) {
      filtered = filtered.filter(commission => 
        (commission.status || '').toLowerCase() === filters.status.toLowerCase()
      );
    }

    // Apply date range filter
    if (filters.startDate) {
      const start = new Date(filters.startDate);
      filtered = filtered.filter(commission => 
        new Date(commission.created_at) >= start
      );
    }
    
    if (filters.endDate) {
      const end = new Date(filters.endDate);
      end.setHours(23, 59, 59, 999); // End of day
      filtered = filtered.filter(commission => 
        new Date(commission.created_at) <= end
      );
    }

    // Apply search filter
    if (filters.search?.trim()) {
      const searchTerm = filters.search.toLowerCase().trim();
      filtered = filtered.filter(commission => {
        const transactionId = formatTransactionId(commission).toLowerCase();
        const customerName = (commission.customer_name || 
          commission.customer?.name || 
          customerDetails[commission.customer_id]?.name || '').toLowerCase();
          
        const recipientName = (commission.recipient_name || 
          (commission.recipient_type === 'admin' ? 'Bright Planet Ventures' : 
            (commission.recipient?.name || 
              promoterDetails[commission.recipient_id]?.name || ''))).toLowerCase();
              
        const amount = String(commission.amount || '').toLowerCase();
        
        return (
          transactionId.includes(searchTerm) ||
          customerName.includes(searchTerm) ||
          recipientName.includes(searchTerm) ||
          amount.includes(searchTerm)
        );
      });
    }

    // Sort by date (newest first)
    filtered.sort((a, b) => new Date(b.created_at) - new Date(a.created_at));
    
    setFilteredHistory(filtered);
    setCurrentPage(1);
  }, [commissionHistory, filters, promoterDetails, customerDetails]);

  // Load initial data on component mount
  useEffect(() => {
    loadInitialCommissionData();
  }, []);

  // Get status display
  const getStatusDisplay = (status) => {
    switch (status) {
      case 'credited':
        return { icon: CheckCircle, color: 'text-green-400', bg: 'bg-green-500/20', label: 'Credited' };
      case 'pending':
        return { icon: Clock, color: 'text-yellow-400', bg: 'bg-yellow-500/20', label: 'Pending' };
      case 'failed':
        return { icon: XCircle, color: 'text-red-400', bg: 'bg-red-500/20', label: 'Failed' };
      default:
        return { icon: AlertCircle, color: 'text-gray-400', bg: 'bg-gray-500/20', label: 'Unknown' };
    }
  };

  // Format currency
  const formatCurrency = (amount) => {
    return `‚Çπ${parseFloat(amount).toLocaleString('en-IN')}`;
  };

  // Show loading skeleton
  if (initialLoading) {
    return (
      <>
        <SharedStyles />
        {/* AdminNavbar removed - handled by ConditionalNavbar in App.js */}
        <UnifiedBackground>
          <SkeletonFullPage type="dashboard" />
        </UnifiedBackground>
      </>
    );
  }

  return (
    <>
      <SharedStyles />
      {/* AdminNavbar removed - handled by ConditionalNavbar in App.js */}
      <UnifiedBackground>
        <div className="min-h-screen pt-40 px-8 pb-8">
          <div className="container mx-auto px-4">
            {/* Header */}
            <div className="flex flex-col md:flex-row md:items-center md:justify-between mb-6">
              <div>
                <h1 className="text-2xl font-bold text-white">Income Management</h1>
                <p className="text-slate-400">Monitor and manage commission distributions and earnings</p>
              </div>
            </div>

            {/* Search and Filter */}
            <div className="bg-slate-800/50 rounded-xl p-4 mb-6 border border-slate-700/50">
              <div className="flex flex-col gap-4">
                {/* Search Bar */}
                <div className="relative">
                  <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                    <Search className="h-4 w-4 text-slate-400" />
                  </div>
                  <input
                    type="text"
                    value={filters.search}
                    onChange={(e) => handleFilterChange('search', e.target.value)}
                    placeholder="Search by Transaction ID, Customer, Recipient, Amount..."
                    className="block w-full pl-10 pr-3 py-2 border border-slate-600 rounded-lg bg-slate-700/50 text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-orange-500"
                  />
                </div>

                {/* Filters Toggle */}
                <div className="flex items-center justify-between">
                  <button
                    onClick={toggleFilters}
                    className="flex items-center gap-2 text-sm text-slate-300 hover:text-white transition-colors"
                  >
                    <Filter className="h-4 w-4" />
                    <span>Advanced Filters</span>
                    {showFilters ? (
                      <ChevronUp className="h-4 w-4" />
                    ) : (
                      <ChevronDown className="h-4 w-4" />
                    )}
                  </button>

                  <div className="flex items-center gap-2 text-sm text-slate-400">
                    <span>Show</span>
                    <select
                      value={entriesPerPage}
                      onChange={(e) => {
                        setEntriesPerPage(Number(e.target.value));
                        setCurrentPage(1);
                      }}
                      className="bg-slate-700/50 border border-slate-600 text-white text-sm rounded-lg focus:ring-orange-500 focus:border-orange-500 block p-1"
                    >
                      <option value="10">10</option>
                      <option value="25">25</option>
                      <option value="50">50</option>
                      <option value="100">100</option>
                    </select>
                    <span>commissions per page</span>
                  </div>
                </div>

                {/* Filters Panel */}
                {showFilters && (
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4 pt-4 border-t border-slate-700/50">
                    <div>
                      <label className="block text-sm font-medium text-slate-300 mb-1">Level</label>
                      <select
                        value={filters.level}
                        onChange={(e) => handleFilterChange('level', e.target.value)}
                        className="w-full bg-slate-700/50 border border-slate-600 text-white text-sm rounded-lg focus:ring-orange-500 focus:border-orange-500 block p-2"
                      >
                        <option value="">All Levels</option>
                        <option value="0">Admin Fallback</option>
                        <option value="1">Level 1 (‚Çπ500)</option>
                        <option value="2">Level 2 (‚Çπ100)</option>
                        <option value="3">Level 3 (‚Çπ100)</option>
                        <option value="4">Level 4 (‚Çπ100)</option>
                      </select>
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-slate-300 mb-1">Status</label>
                      <select
                        value={filters.status}
                        onChange={(e) => handleFilterChange('status', e.target.value)}
                        className="w-full bg-slate-700/50 border border-slate-600 text-white text-sm rounded-lg focus:ring-orange-500 focus:border-orange-500 block p-2"
                      >
                        <option value="">All Status</option>
                        <option value="pending">Pending</option>
                        <option value="credited">Credited</option>
                        <option value="failed">Failed</option>
                      </select>
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-slate-300 mb-1">Date Range</label>
                      <div className="grid grid-cols-2 gap-2">
                        <div className="relative">
                          <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                            <Calendar className="h-4 w-4 text-slate-400" />
                          </div>
                          <input
                            type="date"
                            value={filters.startDate}
                            onChange={(e) => handleFilterChange('startDate', e.target.value)}
                            className="w-full pl-10 bg-slate-700/50 border border-slate-600 text-white text-sm rounded-lg focus:ring-orange-500 focus:border-orange-500 block p-2"
                            placeholder="Start Date"
                          />
                        </div>
                        <div className="relative">
                          <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                            <Calendar className="h-4 w-4 text-slate-400" />
                          </div>
                          <input
                            type="date"
                            value={filters.endDate}
                            onChange={(e) => handleFilterChange('endDate', e.target.value)}
                            className="w-full pl-10 bg-slate-700/50 border border-slate-600 text-white text-sm rounded-lg focus:ring-orange-500 focus:border-orange-500 block p-2"
                            placeholder="End Date"
                          />
                        </div>
                      </div>
                    </div>

                    {(filters.level || filters.status || filters.startDate || filters.endDate) && (
                      <div className="md:col-span-3 flex justify-end">
                        <button
                          onClick={clearFilters}
                          className="text-sm text-orange-400 hover:text-orange-300 transition-colors flex items-center gap-1"
                        >
                          <XCircle className="w-4 h-4" />
                          Clear all filters
                        </button>
                      </div>
                    )}
                  </div>
                )}

                {/* Active Filters */}
                {(filters.level || filters.status || filters.startDate || filters.endDate) && (
                  <div className="pt-3 border-t border-slate-700/50">
                    <div className="flex flex-wrap items-center gap-2">
                      <span className="text-sm text-slate-400">Active filters:</span>
                      {filters.level && (
                        <span className="inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium bg-blue-500/20 text-blue-300">
                          Level: {filters.level === '0' ? 'Admin' : filters.level}
                        </span>
                      )}
                      {filters.status && (
                        <span className="inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium bg-green-500/20 text-green-300">
                          Status: {filters.status.charAt(0).toUpperCase() + filters.status.slice(1)}
                        </span>
                      )}
                      {filters.startDate && (
                        <span className="inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium bg-purple-500/20 text-purple-300">
                          From: {new Date(filters.startDate).toLocaleDateString()}
                        </span>
                      )}
                      {filters.endDate && (
                        <span className="inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium bg-purple-500/20 text-purple-300">
                          To: {new Date(filters.endDate).toLocaleDateString()}
                        </span>
                      )}
                    </div>
                  </div>
                )}
              </div>
            </div>

            {/* Commission History Table */}
            <UnifiedCard className="overflow-hidden" variant="glassDark">
              <div className="p-6 border-b border-gray-700/50">
                <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4">
                  <div>
                    <h2 className="text-lg font-semibold text-white">Income History</h2>
                    <p className="text-sm text-gray-400 mt-1">
                      Track and manage all commission transactions
                    </p>
                  </div>
                  {filteredHistory.length > 0 && (
                    <div className="flex items-center gap-2 px-3 py-1.5 bg-slate-700/30 rounded-lg border border-slate-600/50">
                      <div className="w-2 h-2 bg-green-400 rounded-full animate-pulse"></div>
                      <span className="text-slate-300 text-sm font-medium">
                        Showing <span className="text-green-400 font-bold">
                          {((currentPage - 1) * entriesPerPage) + 1}-{Math.min(currentPage * entriesPerPage, filteredHistory.length)}
                        </span> of <span className="text-green-400 font-bold">
                          {filteredHistory.length.toLocaleString()}
                        </span> records
                      </span>
                    </div>
                  )}
                </div>
              </div>

              <div className="overflow-x-auto">
                {/* Initial loading skeleton */}
                {initialLoading ? (
                  <div className="p-6">
                    {[...Array(5)].map((_, i) => (
                      <div key={i} className="animate-pulse mb-4">
                        <div className="h-4 bg-gray-700 rounded w-3/4 mb-2"></div>
                        <div className="h-4 bg-gray-700 rounded w-1/2"></div>
                      </div>
                    ))}
                  </div>
                ) : filteredHistory.length === 0 ? (
                  <div className="p-12 text-center">
                    <Activity className="w-16 h-16 text-gray-500 mx-auto mb-4" />
                    <h3 className="text-xl font-semibold text-gray-400 mb-2">
                      {Object.values(filters).some(f => f) ? 'No matching records' : 'No Income History'}
                    </h3>
                    <p className="text-gray-500">
                      {Object.values(filters).some(f => f)
                        ? 'Try adjusting your filters to see more results.'
                        : 'Income transactions will appear here once customers are created.'}
                    </p>
                    {Object.values(filters).some(f => f) && (
                      <button
                        onClick={clearFilters}
                        className="mt-4 px-4 py-2 bg-orange-500/10 hover:bg-orange-500/20 text-orange-300 rounded-lg transition-colors text-sm font-medium flex items-center gap-2 mx-auto"
                      >
                        <XCircle className="w-4 h-4" />
                        Clear all filters
                      </button>
                    )}
                  </div>
                ) : (
                  <div className="relative">
                    <table className="w-full">
                      <thead className="bg-slate-800/50">
                        <tr>
                          <th className="px-6 py-3 text-left text-xs font-medium text-slate-300 uppercase tracking-wider">
                            <div className="flex items-center gap-1">
                              <span>Transaction</span>
                            </div>
                          </th>
                          <th className="px-6 py-3 text-left text-xs font-medium text-slate-300 uppercase tracking-wider">
                            Customer
                          </th>
                          <th className="px-6 py-3 text-center text-xs font-medium text-slate-300 uppercase tracking-wider">
                            Level
                          </th>
                          <th className="px-6 py-3 text-left text-xs font-medium text-slate-300 uppercase tracking-wider">
                            Recipient
                          </th>
                          <th className="px-6 py-3 text-right text-xs font-medium text-slate-300 uppercase tracking-wider">
                            Amount
                          </th>
                          <th className="px-6 py-3 text-right text-xs font-medium text-slate-300 uppercase tracking-wider">
                            Date
                          </th>
                        </tr>
                      </thead>
                      <tbody className="divide-y divide-gray-700/50">
                        {filteredHistory.slice((currentPage - 1) * entriesPerPage, currentPage * entriesPerPage).map((commission) => {
                          const statusDisplay = getStatusDisplay(commission.status);
                          const StatusIcon = statusDisplay.icon;

                          return (
                            <tr key={commission.id} className="hover:bg-gray-800/30 transition-colors">
                              <td className="px-8 py-5 whitespace-nowrap text-center">
                                <div className="text-white font-medium font-mono text-sm">
                                  {formatTransactionId(commission)}
                                </div>
                              </td>
                              <td className="px-8 py-5 whitespace-nowrap">
                                <div className="flex flex-col">
                                  <span className="text-white font-medium text-sm">
                                    {commission.customer_name ||
                                      commission.customer?.name ||
                                      customerDetails[commission.customer_id]?.name ||
                                      'Unknown Customer'}
                                  </span>
                                  <span className="text-xs text-gray-400 font-mono">
                                    {commission.customer_display_id ||
                                      commission.customer?.customer_id ||
                                      customerDetails[commission.customer_id]?.customer_id ||
                                      `C-${String(commission.customer_id || '').slice(-4)}`}
                                  </span>
                                </div>
                              </td>
                              <td className="px-8 py-5 whitespace-nowrap text-center">
                                <div className="flex flex-col items-center space-y-1">
                                  {/* Level Badge */}
                                  <div className={`
                                      inline-flex items-center justify-center w-8 h-8 rounded-full font-bold text-sm
                                      ${commission.level === 0 ? 'bg-gray-600 text-gray-200' :
                                      commission.level === 1 ? 'bg-green-500 text-white' :
                                        commission.level === 2 ? 'bg-blue-500 text-white' :
                                          commission.level === 3 ? 'bg-purple-500 text-white' :
                                            commission.level === 4 ? 'bg-orange-500 text-white' :
                                              'bg-gray-500 text-white'}
                                    `}>
                                    {commission.level === 0 ? 'A' : commission.level}
                                  </div>

                                  {/* Level Description */}
                                  <div className={`
                                      text-xs font-medium
                                      ${commission.level === 0 ? 'text-gray-400' :
                                      commission.level === 1 ? 'text-green-300' :
                                        commission.level === 2 ? 'text-blue-300' :
                                          commission.level === 3 ? 'text-purple-300' :
                                            commission.level === 4 ? 'text-orange-300' :
                                              'text-gray-400'}
                                    `}>
                                    {commission.level === 0 ? 'Admin' :
                                      commission.level === 1 ? 'Direct' :
                                        commission.level === 2 ? 'Level 2' :
                                          commission.level === 3 ? 'Level 3' :
                                            commission.level === 4 ? 'Level 4' :
                                              `Level ${commission.level}`}
                                  </div>

                                  {/* Amount Indicator */}
                                  <div className="text-xs text-gray-500 font-mono">
                                    {commission.level === 0 ? 'Fallback' :
                                      commission.level === 1 ? '‚Çπ500' : '‚Çπ100'}
                                  </div>
                                </div>
                              </td>
                              <td className="px-8 py-5 whitespace-nowrap">
                                <div className="flex flex-col">
                                  <span className="text-white font-medium text-sm">
                                    {commission.recipient_name ||
                                      (commission.recipient_type === 'admin' ? 'Bright Planet Ventures' :
                                        (commission.recipient?.name ||
                                          promoterDetails[commission.recipient_id]?.name ||
                                          'Unknown Promoter'))}
                                  </span>
                                  <span className="text-xs text-gray-400 font-mono">
                                    {commission.recipient_display_id ||
                                      (commission.recipient_type === 'admin' ? 'BPVP01' :
                                        (commission.recipient?.promoter_id ||
                                          promoterDetails[commission.recipient_id]?.promoter_id ||
                                          `ID-${String(commission.recipient_id || '').slice(-6).toUpperCase()}`))}
                                  </span>
                                </div>
                              </td>
                              <td className="px-8 py-5 whitespace-nowrap text-center">
                                <div className="text-lg font-bold text-green-300 font-mono">
                                  {formatCurrency(commission.amount)}
                                </div>
                              </td>
                              <td className="px-8 py-5 whitespace-nowrap text-center">
                                <div className="text-sm">
                                  <div className="text-white font-medium">
                                    {commission.created_at ? new Date(commission.created_at).toLocaleDateString('en-IN') : 'Unknown'}
                                  </div>
                                  <div className="text-xs text-gray-400 font-mono">
                                    {commission.created_at ? new Date(commission.created_at).toLocaleTimeString('en-IN', {
                                      hour: '2-digit',
                                      minute: '2-digit',
                                      hour12: true
                                    }) : ''}
                                  </div>
                                </div>
                              </td>
                            </tr>
                          );
                        })}
                      </tbody>
                    </table>
                  </div>
                )}
                
                {/* Bottom Pagination Controls for Commissions */}
                {filteredHistory.length > 0 && (
                  <div className="px-6 py-4 border-t border-gray-700/50 bg-gray-800/30">
                    <div className="flex flex-col sm:flex-row justify-between items-center gap-4">
                      <div className="flex items-center gap-2 px-3 py-1.5 bg-gray-700/50 rounded-lg border border-gray-600/50">
                        <div className="w-2 h-2 bg-green-400 rounded-full animate-pulse"></div>
                        <span className="text-gray-300 text-sm font-medium">
                          Page <span className="text-green-400 font-bold">{currentPage}</span> of <span className="text-green-400 font-bold">{Math.ceil(filteredHistory.length / entriesPerPage)}</span>
                        </span>
                      </div>
                      
                      <div className="flex items-center gap-2">
                        <button
                          onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
                          disabled={currentPage === 1}
                          className={`group relative p-2.5 rounded-lg transition-all duration-200 ${
                            currentPage === 1 
                              ? 'bg-gray-700/50 text-gray-500 cursor-not-allowed border border-gray-600/30' 
                              : 'bg-gray-700 text-gray-300 hover:bg-gray-600 hover:text-white border border-gray-600/50 hover:border-gray-500 hover:shadow-lg hover:shadow-green-400/10'
                          }`}
                          title="Previous Page"
                        >
                          <ChevronLeft className="w-4 h-4" />
                          <span className="absolute -bottom-8 left-1/2 transform -translate-x-1/2 text-xs text-gray-400 opacity-0 group-hover:opacity-100 transition-opacity whitespace-nowrap">Previous</span>
                        </button>
                        
                        <div className="flex items-center gap-1 px-2 py-1 bg-gray-700/30 rounded-lg border border-gray-600/30">
                          {Array.from({ length: Math.min(5, Math.ceil(filteredHistory.length / entriesPerPage)) }, (_, i) => {
                            const totalPages = Math.ceil(filteredHistory.length / entriesPerPage);
                            let pageNumber;
                              
                            if (totalPages <= 5) {
                              pageNumber = i + 1;
                            } else if (currentPage <= 3) {
                              pageNumber = i + 1;
                            } else if (currentPage >= totalPages - 2) {
                              pageNumber = totalPages - 4 + i;
                            } else {
                              pageNumber = currentPage - 2 + i;
                            }
                              
                            return (
                              <button
                                key={pageNumber}
                                onClick={() => setCurrentPage(pageNumber)}
                                className={`w-8 h-8 rounded-md text-sm font-medium transition-all duration-200 ${
                                  currentPage === pageNumber
                                    ? 'bg-gradient-to-r from-green-500 to-green-600 text-white shadow-lg shadow-green-400/20 border border-green-400/50'
                                    : 'bg-gray-600/50 text-gray-300 hover:bg-gray-600 hover:text-white border border-gray-500/30 hover:border-gray-400/50'
                                }`}
                                title={`Go to page ${pageNumber}`}
                              >
                                {pageNumber}
                              </button>
                            );
                          })}
                        </div>
                        
                        <button
                          onClick={() => setCurrentPage(prev => Math.min(prev + 1, Math.ceil(filteredHistory.length / entriesPerPage)))}
                          disabled={currentPage === Math.ceil(filteredHistory.length / entriesPerPage)}
                          className={`group relative p-2.5 rounded-lg transition-all duration-200 ${
                            currentPage === Math.ceil(filteredHistory.length / entriesPerPage)
                              ? 'bg-gray-700/50 text-gray-500 cursor-not-allowed border border-gray-600/30'
                              : 'bg-gray-700 text-gray-300 hover:bg-gray-600 hover:text-white border border-gray-600/50 hover:border-gray-500 hover:shadow-lg hover:shadow-green-400/10'
                          }`}
                          title="Next Page"
                        >
                          <ChevronRight className="w-4 h-4" />
                          <span className="absolute -bottom-8 left-1/2 transform -translate-x-1/2 text-xs text-gray-400 opacity-0 group-hover:opacity-100 transition-opacity whitespace-nowrap">Next</span>
                        </button>
                      </div>
                    </div>
                  </div>
                )}

                {/* Loading more indicator */}
                {loadingMore && (
                  <div className="p-4 text-center text-gray-400">
                    Loading more commissions...
                  </div>
                )}

                {/* End of list message */}
                {!hasMoreData && filteredHistory.length > 0 && (
                  <div className="p-4 text-center text-gray-400 border-t border-gray-700">
                    You've reached the end of the commission history
                  </div>
                )}
              </div>
            </UnifiedCard>

            {/* Toast Notification */}
            {toast.show && (
              <div className={`fixed top-4 right-4 z-50 max-w-md p-4 rounded-lg shadow-lg border transition-all duration-300 ${toast.type === 'success'
                  ? 'bg-green-500/10 border-green-500/30 text-green-300'
                  : 'bg-red-500/10 border-red-500/30 text-red-300'
                }`}>
                <div className="flex items-start space-x-3">
                  <div className={`w-6 h-6 rounded-full flex items-center justify-center ${toast.type === 'success' ? 'bg-green-500' : 'bg-red-500'
                    }`}>
                    {toast.type === 'success' ? (
                      <CheckCircle className="w-4 h-4 text-white" />
                    ) : (
                      <XCircle className="w-4 h-4 text-white" />
                    )}
                  </div>
                  <div className="flex-1">
                    <p className="text-sm font-medium whitespace-pre-line">{toast.message}</p>
                  </div>
                  <button
                    onClick={() => setToast({ show: false, message: '', type: '' })}
                    className="text-gray-400 hover:text-white transition-colors"
                  >
                    <XCircle className="w-4 h-4" />
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>
      </UnifiedBackground>
    </>
  );
}

export default AffiliateCommissions;
